*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color:blueviolet;
}

.card-container{
  width: 90%;
  margin: auto;
  text-align: center;
  padding: 5px;
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap:10px;
 
}

.card{
  width: 280px;
  height: 400px;
  padding: 10px;
  background-color: white;
  border-radius: 10px;
  
}

#img{
  width: 200px;
  height: 200px;
  margin: 10px;
}
#category{
  font-size: 20px;
  font-weight: bolder;
  margin: 25px;

}

#price{
  margin: 20px;

}

#btn{

  font-size: 15px;
  background-color: black;
  border-radius: 10px;
  color: white;
  padding: 5px;
  width: 80%;
}